@import '../../../assets/css/var';
@import '../../../assets/css/theme';

.input_wrap {
  background: $color-input-bg;
  border-radius: 10px;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid $color-a;
  }

  &_active {
    border: 1px solid $color-a;
  }
  &_disabled {
    input {
      text-shadow: 0 0 0 $color-text-default;
      opacity: 0.7;
      cursor: not-allowed;
      color: transparent !important;
    }
  }
  .with_max_input {
    background: $color-input-bg;
    border: none;
    border-radius: 10px;
    font-size: 20px;
    line-height: 24px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: $color-text-default;
    padding: 14px;
    width: 100%;

    &:hover,
    &:focus {
      border: none;
    }
    &::placeholder {
      color: $color-text-default-3;
    }
    input {
      background: $color-input-bg;
      font-size: 20px;
      line-height: 24px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: $color-text-default;
    }
  }
  .max_btn {
    background: $color-max-btn-bg;
    border-radius: 15px;
    border: 1px solid $color-max-button-border;
    padding: 4px 14px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 500;
    line-height: 17px;
    color: $color-max-button-text;
    margin-right: 10px;
    white-space: nowrap;
  }
}
