@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.swap_balance {
  font-family: PingFangSC, PingFang SC;
  font-weight: 400;
  font-size: 14px;
  color: $color-text-default;
  line-height: 20px;
  span:nth-child(1) {
    font-weight: 400;
    opacity: 0.5;
  }
  span:nth-child(2) {
    font-weight: 600;
  }
}

.swap_arrow {
  display: flex;
  justify-content: center;
  padding-bottom: 30%;
  span {
    height: 46px;
    width: 46px;
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
    transform: rotate(-90deg);
    color: $color-text-default;
    background-image: url(../../../assets/images/psm/icon_swap_to.svg);
    &:hover {
      background-image: url(../../../assets/images/homeTokens/icon_swap_active.svg);
    }
    @include lightThemeOnly {
      background-image: url(../../../assets/images/psm/icon_swap_to_light.svg);
      &:hover {
        background-image: url(../../../assets/images/homeTokens/icon_swap_active_light.svg);
      }
    }
  }
}

.swap_detail_label {
  font-family: PingFangSC, PingFang SC;
  font-weight: 400;
  font-size: 14px;
  color: $color-text-default;
  opacity: 0.5;
  line-height: 20px;
  margin-right: 6px;
}

.swap_detail_value {
  font-family: PingFangSC, PingFang SC;
  font-weight: 600;
  font-size: 14px;
  color: $color-text-default;
  line-height: 20px;
}

.usd_price_text {
  font-family: Helvetica;
  font-size: 14px;
  color: $color-text-default-7;
  line-height: 17px;
  padding: 0 0 14px 16px;
}

@media screen and (max-width: 768px) {
  .swap_arrow {
    justify-content: start;
    padding-bottom: 0;
    span {
      transform: rotate(0deg);
    }
  }
}
