@import './var.scss';
@import './mixins';
@import './theme';

.htx-home-page {
  margin: 0 auto;
  max-width: 1200px;
  @include common-content-width;
  // title start
  .page-htx-title {
    padding-top: 56px;
    & > div {
      margin-bottom: 0 !important;
    }
  }
  // title end

  // banner start
  .page-htx-banner {
    img {
      display: block;
      width: 100%;
    }
  }
  // banner end

  // swap start
  .htx-swap {
    margin: 50px 0 60px;
    padding: 40px 40px 30px;
    background: $color-normal-card-bg;
    border-radius: 10px;

    .htx-submit-btn {
      margin-top: 40px;
      padding: 23px 0;
      font-family: Helvetica, Helvetica;
      font-weight: bold;
      font-size: 20px;
      color: #ffffff;
      line-height: 24px;
      text-align: right;
      font-style: normal;
      background: $color-button-primary;
      box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      text-align: center;
      &-disabled {
        opacity: 0.3;
        pointer-events: none;
      }
    }
  }
  // swap end

  // vote start
  .page-htx-vote {
    position: relative;
    padding: 24px 40px 30px;
    background: $color-farming-card-bg;
    border-radius: 10px;
    .vote-passed-icon-wrap {
      position: absolute;
      right: -40px;
      top: -24px;
      height: 40px;
      img {
        height: 100%;
      }
      .vote-passed-icon-pc {
        display: block;
      }
      .vote-passed-icon-mobile {
        display: none;
      }
    }

    .vote-title-sub {
      height: 22px;
      font-weight: 400;
      font-size: 16px;
      color: $color-text-default;
      opacity: 0.7;
      line-height: 22px;
      text-align: left;
      font-style: normal;
      margin-top: 10px;
      @include mobile-only {
        margin-top: 4px;
      }
    }
    .vote-title {
      position: relative;
      display: flex;
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 20px;
      color: $color-text-default;
      line-height: 28px;
      font-style: normal;
      @include mobile-only {
        display: block;
      }
      .vote-title-l {
        display: flex;
        white-space: nowrap;
        @include mobile-only {
          white-space: normal;
        }
        span {
          margin-right: 18px;
        }
        b {
          margin: 0 7px 0 7px;
          font-size: 30px;
          color: rgba(255, 172, 0, 1);
          line-height: 30px;
          vertical-align: bottom;
        }
      }

      .vote-title-r {
        flex: 1;
        justify-content: space-between;
        display: flex;
      }

      .vote-title-icon-wrap-m {
        display: none;
      }

      .vote-title-icon {
        display: block;
        margin-top: 5px;
        height: 24px;
      }
    }

    .vote-title-sub {
      height: 17px;
      font-size: 12px;
    }

    .vote-time {
      margin-top: 10px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      font-size: 16px;
      color: $color-text-default-7;
      line-height: 22px;
      font-style: normal;
    }

    .page-htx-proposal {
      display: flex;
      align-items: center;
      margin-top: 29px;
      .vote-title-wallet {
        padding: 8px 18px;
        font-weight: 600;
        line-height: 20px;
        font-size: 14px;
        background: $color-button-primary;
        border-radius: 20px;
        color: #ffffff;
        cursor: pointer;
        white-space: nowrap;
        display: inline-block;
      }
      .page-htx-proposal-l {
        flex: 1;
        width: 0;
        display: flex;
        align-items: center;
        margin-right: 20px;
        .page-htx-proposal-icon {
          margin-right: 20px;
          img {
            display: block;
            width: 92px;
          }
        }
        .page-htx-proposal-title1 {
          font-family: PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 16px;
          color: $color-text-default;
          line-height: 22px;
          font-style: normal;
        }
      }
      .page-htx-proposal-r {
        display: flex;
        padding: 15px 0;
        width: 560px;
        background: $color-input-bg-1;
        border-radius: 10px;
        border: 1px solid $color-text-default-1;
        &-wallet {
          width: 560px;
          text-align: right;
        }
        .htx-proposal-item {
          display: flex;
          flex: 1;
          &:first-child {
            border-right: 1px solid $color-text-default-1;
          }
        }
        .htx-proposal-label-wrap {
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .htx-proposal-label {
          padding-left: 20px;
          margin-right: 10px;
          font-weight: 400;
          font-size: 14px;
          color: $color-text-default-5;
          line-height: 20px;
          font-style: normal;
          white-space: nowrap;
        }
        .mr5 {
          margin-right: 5px !important;
        }
        .htx-proposal-label-question {
          margin-right: 10px;
          color: $color-text-default-7;
        }
        .htx-proposal-value {
          padding-right: 10px;
          flex: 1;
          width: 0;
          font-family: Helvetica, Helvetica;
          font-weight: bold;
          font-size: 16px;
          color: $color-text-default;
          line-height: 19px;
          font-style: normal;
        }
      }
    }
    .page-htx-proposal-desc {
      margin: 30px 0;
      padding: 20px;
      line-height: 22px;
      font-size: 14px;
      background: $color-htx-card-bg-default;
      border-radius: 10px;
      color: $color-text-default-7;
      @include lightThemeOnly {
        border: 1px solid #e5e5e5;
      }
    }

    .page-htx-vote-main {
      .page-htx-vote-percent-c-mobile {
        display: none;
      }
      .page-htx-vote-percent-t {
        display: flex;
        justify-content: space-between;
        margin-bottom: 4px;
        .page-htx-vote-percent-img {
          height: 63px;
          img {
            display: block;
            height: 100%;
          }
        }
      }

      .page-htx-vote-percent-c {
        display: flex;
        height: 40px;

        .page-htx-vote-percent-l {
          position: relative;
          min-width: 110px;
          background: linear-gradient(130deg, #ff7a00 0%, #ffaf00 100%);
          border-radius: 10px 0 0 10px;
          border: 2px solid rgba(255, 255, 255, 0.5);
          border-right: 0;
          transition: width 0.3s ease-in;
          span {
            position: absolute;
            left: 30px;
            top: 0;
            font-family: Impact;
            font-size: 24px;
            color: #ffffff;
            line-height: 38px;
            font-style: normal;
          }
        }
        .page-htx-vote-vs {
          position: absolute;
          left: 0;
          top: 50%;
          transform: translate(-50%, -50%);
          img {
            display: block;
            width: 73px;
          }
        }
        .page-htx-vote-percent-r {
          min-width: 120px;
          position: relative;
          flex: 1;
          background: linear-gradient(222deg, #4148ff 0%, #6d54ff 100%);
          border-radius: 0 10px 10px 0;
          border: 2px solid $color-text-default-5;
          border-left: 0;
          transition: width 0.3s ease-in;
          span {
            position: absolute;
            top: 0;
            right: 30px;
            font-family: Impact;
            font-size: 24px;
            color: #ffffff;
            line-height: 38px;
            font-style: normal;
          }
        }
      }

      .page-htx-vote-percent-b {
        display: flex;
        justify-content: space-between;
        margin-top: 12px;
        .page-htx-vote-percent-b-item {
          font-family: PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 18px;
          color: $color-text-default;
          line-height: 25px;
          font-style: normal;
        }
      }

      .page-htx-vote-area {
        display: flex;
        align-items: center;
        min-height: 109px;
        margin-bottom: 12px;

        button {
          display: block;
          margin: 0;
          padding: 0;
          width: 300px;
          height: 70px;
          line-height: 70px;
          background: $color-icon-warning;
          box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.1);
          border-radius: 10px;
          color: #ffffff;
          font-size: 20px;
          &:disabled {
            opacity: 0.3;
          }
        }
        .page-htx-vote-area-r {
          button {
            background: #564eff;
          }
        }
        .page-htx-vote-area-c {
          flex: 1;
          width: 0;
        }
      }

      .page-htx-vote-refresh {
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        font-style: normal;
        color: $color-text-default-7;
        .page-htx-vote-refresh-title {
          margin-right: 16px;
          color: $color-text-default-7;
        }
        .page-htx-vote-refresh-desc {
          margin-right: 6px;
          color: rgba(193, 160, 255, 1);
          cursor: pointer;
        }
        .anticon {
          vertical-align: middle;
        }
      }

      .page-htx-vote-rules {
        margin-top: 50px;
        padding: 20px;
        background: $color-htx-card-bg-default;
        border-radius: 10px;
        @include lightThemeOnly {
          border: 1px solid #e5e5e5;
        }
        .page-htx-vote-rules-title {
          display: flex;
          align-items: center;
          justify-content: space-between;
          .page-htx-vote-rules-title-l {
            font-family: PingFangSC, PingFang SC;
            font-weight: 600;
            font-size: 20px;
            color: $color-text-default;
            line-height: 28px;
            font-style: normal;
          }

          .page-htx-vote-rules-title-r {
            padding: 9px 12px;
            display: flex;
            align-items: center;
            background: $color-home-htx-ploy-bg;
            border-radius: 5px;
            img {
              display: block;
              margin-right: 10px;
              width: 18px;
            }
            span {
              font-family: PingFangSC, PingFang SC;
              font-weight: 400;
              font-size: 12px;
              color: $color-button-link-text;
              line-height: 17px;
              font-style: normal;
            }
          }
        }
        .page-htx-vote-rules-list {
          margin-top: 16px;
          ul {
            li {
              position: relative;
              padding-left: 12px;
              font-family: PingFangSC, PingFang SC;
              font-weight: 400;
              font-size: 14px;
              color: #ffffff;
              line-height: 22px;
              font-style: normal;
              word-break: break-word !important;
              @include lightThemeOnly {
                color: rgba(51, 51, 51, 0.7);
              }
              &::after {
                content: ' ';
                position: absolute;
                left: 0;
                top: 8px;
                width: 6px;
                height: 6px;
                border-radius: 6px;
                background: $color-text-default-3;
              }
            }
          }
        }
      }
    }

    .vote-countdown {
      .vote-countdown-title {
        margin-bottom: 12px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 600;
        font-size: 20px;
        color: $color-text-default;
        line-height: 28px;
        font-style: normal;
        text-align: center;
      }
      .vote-countdown-list {
        display: flex;
        justify-content: center;
        margin: 0 auto;
        .vote-countdown-item {
          margin-right: 10px;
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
    .count-down-item {
      .count-down-item-body {
        position: relative;
        .count-down-item-bg {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          .count-down-item-bg1 {
            margin-bottom: 2px;
            flex: 1;
            width: 100%;
            background: $color-input-bg-1;
            border-radius: 10px 10px 0px 0px;
            border: 1px solid $color-text-default-1;
          }
          .count-down-item-bg2 {
            flex: 1;
            width: 100%;
            background: $color-input-bg-1;
            border-radius: 0px 0px 10px 10px;
            border: 1px solid $color-text-default-1;
          }
        }
        .count-down-number {
          position: relative;
          z-index: 20;
          padding: 0 5px;
          min-width: 54px;
          height: 48px;
          font-family: Helvetica, Helvetica;
          font-weight: bold;
          font-size: 40px;
          color: $color-text-default;
          line-height: 48px;
          text-align: right;
          font-style: normal;
        }
      }
    }
    .count-down-desc {
      margin-top: 7px;
      text-align: center;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      font-size: 10px;
      color: $color-text-default-5;
      line-height: 14px;
      font-style: normal;
    }
  }

  .page-htx-proposal-status {
    display: inline-block;
    padding: 2px 7px;
    line-height: 17px;
    background: rgba(255, 172, 0, 0.1);
    border-radius: 2px;
    color: rgba(255, 172, 0, 1);
    margin-left: 10px;
    @include mobile-only {
      margin-left: 0;
    }
  }
  .page-htx-proposal-status-active {
    background: rgba(255, 172, 0, 0.1);
    color: rgba(255, 172, 0, 1);
  }
  .page-htx-proposal-status-passed {
    background: rgba(40, 202, 186, 0.1);
    color: rgba(40, 202, 186, 1);
  }
  .page-htx-proposal-status-excused {
    background: rgba(193, 160, 255, 0.1);
    color: rgba(193, 160, 255, 1);
  }
  // vote end
}

.home-swap-error-text {
  font-size: 12px;
  font-weight: 400;
  color: $warning;
  line-height: 17px;
  text-align: left;
  margin-bottom: 10px;
}

@keyframes loadingPoint {
  25% {
    content: '·';
  }
  50% {
    content: '··';
  }
  75% {
    content: '···';
  }
  100% {
    content: '';
  }
}

@media screen and (max-width: 1079px) {
  .htx-home-page {
    padding: 0 15px;
    // title start
    .page-htx-title {
      padding-top: 10px;
    }
    // title end

    // swap start
    .htx-swap {
      margin: 50px 0 60px;
      padding: 12px 10px;

      .htx-swap-body {
        display: block;
        .htx-swap-arrow {
          margin: 12px 0;
          width: 30px;
          height: 30px;
          background: url('../../assets/images/swap-arrow-down.png') 100%/100% no-repeat;
          &:hover {
            background: url('../../assets/images/swap-double-arrow.png') 100%/100% no-repeat;
            transform: rotate(90deg);
          }
        }
      }

      .htx-swap-header {
        .htx-swap-header-l {
          .htx-swap-header-logo {
            margin-right: 6px;
          }
          .htx-swap-header-name {
            margin-right: 10px;
            font-size: 14px;
            line-height: 20px;
          }
          .htx-swap-header-jump {
            width: 22px;
            cursor: pointer;
          }
        }

        .htx-swap-header-r {
          line-height: 17px;
          font-size: 12px;
          .htx-swap-header-r-label {
            margin-right: 4px;
          }
        }
      }

      .htx-swap-input {
        margin-top: 10px;

        .max-btn {
          top: 10px;
          padding: 2px 12px;
          font-size: 11px;
          line-height: 16px;
        }
        .ant-input {
          height: 44px !important;
          padding-left: 10px;
          padding-right: 10px;
          font-size: 20px;
          line-height: 24px;
        }
        .bottom-price-loading {
          padding: 0 16px 16px;
          font-size: 12px;
          font-weight: 600;
          color: rgba(255, 255, 255, 0.5);
          line-height: 17px;
          text-align: left;
        }
        .price-loading-img {
          position: relative;
          &:after {
            animation: loadingPoint 2000ms infinite;
            position: absolute;
            content: '';
            left: 10px;
            top: 50%;
            transform: translateY(-50%);
            letter-spacing: 3px;
            font-size: 15px;
          }
        }
      }

      .htx-swap-footer {
        margin: 20px 0 0;
        .htx-swap-rate {
          display: flex;
          align-items: center;
          text-align: left;
          color: $color-text-default;
          .swap-rate-label {
            color: $color-text-default-5;
          }
          .mr8 {
            margin-right: 8px;
          }
        }
      }

      .htx-submit-btn {
        margin-top: 20px;
        padding: 9px 0;
        font-size: 16px;
        line-height: 22px;
        border-radius: 5px;
      }
    }
    // swap end

    // vote start
    .page-htx-vote {
      padding: 12px 10px 10px;
      border-radius: 10px;
      .vote-passed-icon-wrap {
        top: initial;
        right: -10px;
        bottom: -12px;
        height: 30px;
        .vote-passed-icon-pc {
          display: none;
        }
        .vote-passed-icon-mobile {
          display: block;
        }
      }

      .vote-title {
        display: block;
        //margin-bottom: 20px;
        font-size: 16px;
        line-height: 24px;
        .vote-title-l {
          display: block;
          flex: initial;
          width: 100%;
          span {
            margin-right: 0;
          }
          b {
            font-size: 16px;
            line-height: inherit;
            vertical-align: bottom;
            color: rgba(255, 172, 0, 1);
          }
        }

        .vote-title-r {
          display: flex;
          //justify-content: flex-end;
          align-items: center;
          margin-top: 4px;
        }

        .vote-title-icon {
          // display: none;
        }
        .vote-title-icon-wrap-m {
          display: block;
          .vote-title-icon {
            display: block;
          }
        }
      }

      .vote-time {
        margin-top: 12px;
        font-size: 12px;
        line-height: 17px;
      }

      .page-htx-proposal {
        display: block;
        margin-top: 21px;
        .vote-title-wallet {
          width: 100%;
          line-height: 36px;
          height: 36px;
          background: $color-button-primary;
          border-radius: 20px;
          text-align: center;
          padding: 0;
          margin-top: 19px;
        }
        .page-htx-proposal-l {
          width: 100%;
          margin-right: 0;
          align-items: flex-start;
          .page-htx-proposal-icon {
            margin-right: 7px;
            width: 65px;
            img {
              width: 65px;
            }
          }
          .page-htx-proposal-title1 {
            margin-bottom: 5px;
            font-size: 14px;
            line-height: 20px;
          }
        }
        .page-htx-proposal-r {
          width: 100%;
          border-radius: 5px;
          margin-top: 19px;
          &-wallet {
            width: 100%;
          }
          .htx-proposal-item {
            display: block;
            text-align: center;
          }
          .htx-proposal-label {
            padding-left: 0;
            margin-right: 0;
            font-size: 12px;
            line-height: 17px;
          }
          .htx-proposal-value {
            padding-right: 0;
            width: 100%;
            margin-top: 4px;
          }
        }
      }
      .page-htx-proposal-desc {
        margin: 16px 0;
        padding: 10px;
        line-height: 18px;
        font-size: 12px;
        border-radius: 5px;
      }

      .page-htx-vote-main {
        .page-htx-vote-percent-c-mobile {
          display: block;
          margin: 0 auto;
        }
        .page-htx-vote-percent {
          margin-top: -33px;
        }
        .page-htx-vote-percent-passed {
          margin-top: -10px;
        }
        .page-htx-vote-percent-t {
          .page-htx-vote-percent-img {
            height: 40px;
          }
        }

        .page-htx-vote-percent-c {
          display: flex;
          height: 26px;

          .page-htx-vote-percent-l {
            min-width: 50px;
            border-radius: 5px 0 0 5px;
            span {
              left: 4px;
              font-size: 16px;
              line-height: 24px;
            }
          }
          .page-htx-vote-vs {
            img {
              width: 46px;
            }
          }
          .page-htx-vote-percent-r {
            min-width: 80px;
            border-radius: 0 5px 5px 0;
            span {
              right: 4px;
              font-size: 16px;
              line-height: 24px;
            }
          }
        }

        .page-htx-vote-percent-b {
          margin-top: 8px;
          .page-htx-vote-percent-b-item {
            font-size: 12px;
            line-height: 17px;
          }
        }

        .page-htx-vote-area {
          min-height: 40px;
          margin-top: 18px;
          margin-bottom: 10px;

          button {
            width: 100%;
            height: 40px;
            line-height: 40px;
            border-radius: 5px;
            font-size: 14px;
          }
          .page-htx-vote-area-l,
          .page-htx-vote-area-r {
            flex: 1;
          }
          .page-htx-vote-area-l {
            margin-right: 25px;
          }
          .page-htx-vote-area-c {
            display: none;
          }
        }

        .page-htx-vote-refresh {
          font-size: 12px;
          line-height: 17px;
          .page-htx-vote-refresh-title {
            display: block;
            margin-right: 0;
            margin-bottom: 6px;
          }
          .page-htx-vote-refresh-desc {
            margin-right: 8px;
          }
        }

        .page-htx-vote-rules {
          margin-top: 30px;
          padding: 10px;
          border-radius: 5px;
          .page-htx-vote-rules-title {
            .page-htx-vote-rules-title-l {
              font-size: 16px;
              line-height: 22px;
            }

            .page-htx-vote-rules-title-r {
              padding: 0;
              background: transparent;

              img {
                margin-right: 6px;
              }
            }
          }
          .page-htx-vote-rules-list {
            margin-top: 10px;
            ul {
              li {
                padding-left: 10px;
                font-size: 12px;
                line-height: 18px;
                word-break: break-word !important;
              }
            }
          }
        }
      }

      .vote-countdown {
        .vote-countdown-title {
          margin-bottom: 6px;
          font-size: 14px;
          line-height: 20px;
        }
        .vote-countdown-list {
          .vote-countdown-item {
            margin-right: 4px;
            &:last-child {
              margin-right: 0;
            }
          }
        }
      }
      .count-down-item {
        .count-down-item-body {
          .count-down-number {
            padding: 0 3px;
            font-size: 26px;
            line-height: 40px;
            min-width: 34px;
            height: 40px;
          }
        }
      }
      .count-down-desc {
        margin-top: 4px;
      }
    }
    // vote end
  }
}
.htx-proposal-tooltip-card {
  .htx-proposal-tooltip {
    position: relative;
    padding-left: 10px;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background: $color-text-default;
      top: 7px;
    }
  }
}
