@import '../../../assets/css/theme';

.token_address {
  img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;
  }

  span:nth-child(2) {
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 16px;
    color: $color-text-default;
    line-height: 22px;
  }

  .token_view_icon {
    width: 28px;
    height: 28px;
    border: 1px solid $color-text-default-1;
    color: $color-text-default;
    border-radius: 50%;
    text-align: center;
    display: inline-block;
    line-height: 30px;
    cursor: pointer;
    &:hover {
      border: 1px solid $color-a-hover;
      color: $color-a-hover;
    }
  }
}
